        let dy = p2.y - p1.y;
        let invdx = dx === 0 ? 0 : 1 / dx;
        let invdy = dy === 0 ? 0 : 1 / dy;
        let cornerX = Infinity;
        let cornerY = Infinity;

        // Check each side of the rectangle for a single-side intersection
